import { FC } from "react";

interface Socials{
  facebook?: string;
  linked_in?: string;
  youtube?: string;
  instagram?: string;
}
export interface SocialsListProps {
  className?: string;
  itemClass?: string;
  socials?: Socials;
}

// const socialsDemo: SocialType[] = [
//   { name: "Facebook", icon: "lab la-facebook-square", href: "#" },
//   { name: "Linked in", icon: "lab la-linked-in", href: "#" },
//   { name: "Youtube", icon: "lab la-youtube", href: "#" },
//   { name: "Instagram", icon: "lab la-instagram", href: "#" },
// ];

const SocialsList: FC<SocialsListProps> = ({
  className = "",
  itemClass = "block",
  socials = {},
}) => {

  return (
    <nav
      className={`nc-SocialsList flex space-x-2.5 text-2xl text-neutral-6000 dark:text-neutral-300 ${className}`}
      data-nc-id="SocialsList"
    >
      {socials.facebook && <a className={`${itemClass}`} href={socials.facebook} target="_blank" rel="noopener noreferrer" title={"facebook"}>
        <i className={"lab la-facebook-square"}></i>
      </a>}
      {socials.linked_in && <a className={`${itemClass}`} href={socials.linked_in} target="_blank" rel="noopener noreferrer" title={"linked_in"}>
        <i className={"lab la-linkedin"}></i>
      </a>}
      {socials.youtube && <a className={`${itemClass}`} href={socials.youtube} target="_blank" rel="noopener noreferrer" title={"youtube"}>
        <i className={"lab lab la-youtube"}></i>
      </a>}
      {socials.instagram && <a className={`${itemClass}`} href={socials.instagram} target="_blank" rel="noopener noreferrer" title={"instagram"}>
        <i className={"lab lab la-instagram"}></i>
      </a>}
    </nav>
  );
};

export default SocialsList;
