import { usePutTourImagesMutation } from "features/toursApiSlice";
import { FC, useState } from "react";
import ButtonPrimary from "shared/Button/ButtonPrimary";
import CommonLayout from "./CommonLayout";

export interface PageAddListing7Props { }

const PageAddListing7: FC<PageAddListing7Props> = () => {

  const [putTourImages] = usePutTourImagesMutation();

  const [successMsg, setSuccessMsg] = useState('');
  const [errMsg, setErrMsg] = useState('');

  const handleFormSubmit = async (event) => {
    event.preventDefault();

    const photos = event.target[0].files as File[];
    const tour_id = sessionStorage.getItem('created-tour-id');

    for (let photo of photos) {
      const tourImagesData = new FormData();
      tourImagesData.append('tour_id', String(tour_id));
      tourImagesData.append('tour_images', photo, photo.name);
      putTourImages({ fileName: photo.name, imagesData: tourImagesData }).unwrap().then((res) => {
        setSuccessMsg("Success");
      }
      ).catch((err) => {
        if (err.status == 400) {
          setErrMsg('One of felids is invalid');
        } else if (err.originalStatus === 401) {
          setErrMsg('Unauthorized');
        } else if (!err?.originalStatus) {
          setErrMsg('No Server Response');
        } else {
          setErrMsg('Something went wrong');
        }
      })
    }
  }

  const [uploadedFiles, setUploadedFiles] = useState<number>(0);

  const handleFileUploading = (e) => {
    const files = e.target.files;
    const allFiles = Array.from(files);
    setUploadedFiles(allFiles.length);
  }

  return (
    <CommonLayout
      index="2"
      backtHref="/add-listing-1"
      nextHref="/add-listing-10"
      nextBtnText="Watch result"
    >
      <form onSubmit={handleFormSubmit}>
        <div>
          <h2 className="text-2xl font-semibold">Pictures of the place</h2>
          <span className="block mt-2 text-neutral-500 dark:text-neutral-400">
            A few beautiful photos will help customers have more sympathy for
            your property.
          </span>
        </div>

        <div className="w-14 border-b border-neutral-200 dark:border-neutral-700"></div>
        {/* FORM */}
        <div className="space-y-8">
          <div>
            <span className="text-lg font-semibold">Pictures of the place</span>
            <div className="mt-5 ">
              <div className="mt-1 flex justify-center px-6 pt-5 pb-6 border-2 border-neutral-300 dark:border-neutral-6000 border-dashed rounded-md">
                <div className="space-y-1 text-center">
                  <svg
                    className="mx-auto h-12 w-12 text-neutral-400"
                    stroke="currentColor"
                    fill="none"
                    viewBox="0 0 48 48"
                    aria-hidden="true"
                  >
                    <path
                      d="M28 8H12a4 4 0 00-4 4v20m32-12v8m0 0v8a4 4 0 01-4 4H12a4 4 0 01-4-4v-4m32-4l-3.172-3.172a4 4 0 00-5.656 0L28 28M8 32l9.172-9.172a4 4 0 015.656 0L28 28m0 0l4 4m4-24h8m-4-4v8m-12 4h.02"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    ></path>
                  </svg>
                  <div className="flex text-sm text-neutral-6000 dark:text-neutral-300">
                    <label
                      htmlFor="file-upload-2"
                      className="relative cursor-pointer  rounded-md font-medium text-primary-6000 hover:text-primary-500 focus-within:outline-none focus-within:ring-2 focus-within:ring-offset-2 focus-within:ring-primary-500"
                    >
                      <span>Upload a file</span>
                      <input
                        id="file-upload-2"
                        name="file-upload-2"
                        type="file"
                        className="sr-only"
                        multiple
                        onChange={handleFileUploading}
                      />
                    </label>
                    <p className="pl-1">or drag and drop</p>
                  </div>
                  <p className="text-base text-neutral-500 dark:text-neutral-400">
                   You uploaded <span className="font-bold text-xl" style={{color: "rgb(79, 70, 229)"}}>{uploadedFiles}</span> files
                  </p>
                  <p className="text-xs text-neutral-500 dark:text-neutral-400">
                    PNG, JPG, GIF up to 10MB
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <ButtonPrimary type="submit" className="mt-10 w-full">Upload</ButtonPrimary>
        {errMsg && <div className="text-red-500 text-center mt-5" style={{ borderRadius: "16px" }}>{errMsg}</div>}
        {successMsg && <div className="text-green-500 text-center mt-5" style={{ borderRadius: "16px" }}>{successMsg}</div>}
      </form>
    </CommonLayout>
  );
};

export default PageAddListing7;
