import { apiSlice } from "../app/api/apiSlice";

export const authApiSlice = apiSlice.injectEndpoints({
    endpoints: (builder) => ({
        login: builder.mutation({
            query: (credentials) => ({
                url: 'auth/jwt/create/',
                method: 'POST',
                body: {...credentials},
            }),
        }),
        signUp: builder.mutation({
            query: (credentials) => ({
                url: 'auth/users/',
                method: 'POST',
                body: {...credentials},
            }),
        }),
        getUserIdandEmail: builder.mutation({
            query: (credentials) => ({
                url: 'auth/users/me/',
                method: 'GET',
            }),
        }),
        userActivation: builder.mutation({
            query: (credentials) => ({
                url: 'auth/users/activation/',
                method: 'POST',
                body: credentials,
            }),
        }),
        passwordReset: builder.mutation({
            query: (credentials) => ({
                url: 'auth/users/reset_password/',
                method: 'POST',
                body: credentials,
            }),
        }),
        passwordResetConfirm: builder.mutation({
            query: (credentials) => ({
                url: 'auth/users/reset_password_confirm/',
                method: 'POST',
                body: credentials,
            }),
        }),
    }),
});

export const { useLoginMutation, useSignUpMutation, useGetUserIdandEmailMutation, useUserActivationMutation, usePasswordResetMutation, usePasswordResetConfirmMutation } = authApiSlice
