import TourCard from "components/TourCard/TourCard";
import { Tour } from "data/types";
import { useGetTourByIdMutation } from "features/toursApiSlice";
import { useCallback, useEffect, useState } from "react";
import { useSelector } from "react-redux";
import CommonLayout from "./CommonLayout";

const AccountSavelists = () => {

  //@ts-ignore
  const profile = useSelector((state) => state.profile.profile);

  const toursArray = profile.favorites.map(item => item.tour);

  const [getTour] = useGetTourByIdMutation();

  const [saveTours, setSaveTours] = useState<Tour[]>([]);

  const renderCard = (tour: Tour) => {
    return <TourCard key={tour.id} data={tour} />
  };

  useEffect(
    useCallback(() => {
      for (let tour of toursArray) {
        getTour(tour)
          .unwrap()
          .then((res) => {
            setSaveTours((prev) => [...prev, res]);
          });
      }
    }, [getTour, toursArray]),
    []
  );



  const renderSection1 = () => {
    return (
      <div className="space-y-6 sm:space-y-8">
        <div>
          <h2 className="text-3xl font-semibold">Save list</h2>
        </div>
        <div className="w-14 border-b border-neutral-200 dark:border-neutral-700"></div>

        <div>
          <div className="grid grid-cols-1 gap-6 md:gap-8 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
            {saveTours.map((tourId) => renderCard(tourId))}
          </div>
          {/* <div className="flex mt-11 justify-center items-center">
            <ButtonSecondary>Show me more</ButtonSecondary>
          </div> */}
        </div>
      </div>
    );
  };

  return (
    <div>
      <CommonLayout>{renderSection1()}</CommonLayout>
    </div>
  );
};

export default AccountSavelists;
