import FormItem from "containers/PageAddListing1/FormItem";
import { Profile } from "data/types";
import { usePutProfilePassportMutation, usePutProfilePhoneMutation } from "features/profileApiSlice";
import { FC, useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import { useSelector } from "react-redux";
import ButtonPrimary from "shared/Button/ButtonPrimary";
import Input from "shared/Input/Input";
import CommonLayout from "./CommonLayout";

export interface AccountHostRegistrationProps {
  className?: string;
}

interface FormData {
  phone: string;
}

const AccountHostRegistration: FC<AccountHostRegistrationProps> = ({ className = "" }) => {

  //@ts-ignore
  const profile: Profile = useSelector(state => state.profile.profile);

  const initFormData = {
    phone: "",
  }

  const [formData, setFormData] = useState(initFormData as FormData);
  const [successMsg, setSuccessMsg] = useState('');
  const [errMsg, setErrMsg] = useState('');

  const [putPassport] = usePutProfilePassportMutation();
  const [putPhone] = usePutProfilePhoneMutation();

  const handleFormChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleFormSubmit = async (event) => {
    event.preventDefault();

    const photos = event.target[0].files as File[];

    for (let photo of photos) {
      const passportData = new FormData();
      passportData.append('user_id', String(profile.id));
      passportData.append('passport_photos', photo, photo.name);
      putPassport({ fileName: photo.name, passportData }).unwrap();
    }

    putPhone({ telephone: formData.phone, user_id: profile.id, is_host: false }).unwrap().then((res) => {
      setSuccessMsg("Success");
    }
    ).catch((err) => {
      setErrMsg("Error");
    });

  }

  const [uploadedFiles, setUploadedFiles] = useState<number>(0);

  const handleFileUploading = (e) => {
    const files = e.target.files;
    const allFiles = Array.from(files);
    setUploadedFiles(allFiles.length);
  }

  useEffect(() => {
    setErrMsg('');
    setSuccessMsg('');
  }, [formData])

  return (
    <div className={`nc-AccountPage ${className}`} data-nc-id="AccountPage">
      <Helmet>
        <title>Account || Booking React Template</title>
      </Helmet>
      {(() => {
        const isBloked = profile?.host?.rejected;
        const isAccepted = profile?.host?.is_host;
        const isPending = profile?.host?.telephone;

        if (isBloked) {
          return (
            <CommonLayout>
               <h2 className="text-3xl font-semibold">Host registration</h2>
              <div className="w-14 border-b border-neutral-200 dark:border-neutral-700 mt-10"></div>
              <div className="text-left text-xl font-semibold mt-20">
                Status: <span className="text-red-500 ">Your aplication was rejected!</span>
              </div>
            </CommonLayout>
          )
        } else if (isAccepted) {
          return (
            <CommonLayout>
              <h2 className="text-3xl font-semibold">Host registration</h2>
              <div className="w-14 border-b border-neutral-200 dark:border-neutral-700 mt-10"></div>
              <div className="text-left text-xl font-semibold mt-20">
                Status: <span className="text-green-500 ">Your aplication was accepted!</span>
              </div>
            </CommonLayout>
          )
        } else if (isPending) {
          return (
            <CommonLayout>
               <h2 className="text-3xl font-semibold">Host registration</h2>
              <div className="w-14 border-b border-neutral-200 dark:border-neutral-700 mt-10"></div>
              <div className="text-left text-xl font-semibold mt-20">
                Status: <span className="text-gray-500 ">Your aplication is processing!</span>
              </div>
            </CommonLayout>
          )
        } else {
          return (
            <CommonLayout>
              <div className="space-y-6 sm:space-y-8">
                {/* HEADING */}
                <h2 className="text-3xl font-semibold">Host registration</h2>
                <div className="w-14 border-b border-neutral-200 dark:border-neutral-700"></div>
                {errMsg && <div className="text-red-500 text-center" style={{ borderRadius: "16px" }}>{errMsg}</div>}
                {successMsg && <div className="text-green-500 text-center" style={{ borderRadius: "16px" }}>{successMsg}</div>}
                <form className="flex flex-col md:flex-row" onSubmit={handleFormSubmit}>
                  <div className="flex-shrink-0 flex items-start">
                    <div className="relative  overflow-hidden flex">
                      <div style={{ width: "300px", height: "300px" }}></div>
                      <div className="absolute inset-0 bg-black bg-opacity-60 flex flex-col items-center justify-center text-neutral-50 cursor-pointer">
                        <svg
                          width="30"
                          height="30"
                          viewBox="0 0 30 30"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            d="M17.5 5H7.5C6.83696 5 6.20107 5.26339 5.73223 5.73223C5.26339 6.20107 5 6.83696 5 7.5V20M5 20V22.5C5 23.163 5.26339 23.7989 5.73223 24.2678C6.20107 24.7366 6.83696 25 7.5 25H22.5C23.163 25 23.7989 24.7366 24.2678 24.2678C24.7366 23.7989 25 23.163 25 22.5V17.5M5 20L10.7325 14.2675C11.2013 13.7988 11.8371 13.5355 12.5 13.5355C13.1629 13.5355 13.7987 13.7988 14.2675 14.2675L17.5 17.5M25 12.5V17.5M25 17.5L23.0175 15.5175C22.5487 15.0488 21.9129 14.7855 21.25 14.7855C20.5871 14.7855 19.9513 15.0488 19.4825 15.5175L17.5 17.5M17.5 17.5L20 20M22.5 5H27.5M25 2.5V7.5M17.5 10H17.5125"
                            stroke="currentColor"
                            strokeWidth={1.5}
                            strokeLinecap="round"
                            strokeLinejoin="round"
                          />
                        </svg>
                        {!uploadedFiles && <span className="mt-1 text-m text-center">Add your passport images <br /> (1 or more)</span>}
                        {uploadedFiles !== 0 && <span className="text-m text-center my-5 font-medium">You uploaded <span className="font-bold text-xl" style={{ color: "rgb(79, 70, 229)" }}>{uploadedFiles}</span> file(s)</span>}
                      </div>
                      <input
                        type="file"
                        className="absolute inset-0 opacity-0 cursor-pointer"
                        name="passport"
                        required
                        multiple
                        accept="image/png, image/jpeg, image/jpg, image/webp"
                        onChange={handleFileUploading}
                      />
                    </div>
                  </div>
                  <div className="flex-grow mt-10 md:mt-0 md:pl-16 max-w-3xl space-y-6" >
                    <div>
                      <FormItem label="Phone number" desc="Enter your phone in international format without +">
                        <Input
                          className="mt-1.5"
                          // defaultValue="Eden Tuan"
                          name="phone"
                          type="phone"
                          required
                          value={formData.phone}
                          onChange={handleFormChange}
                          style={{ border: "1px solid white" }}
                        />
                      </FormItem>
                    </div>
                    <div className="pt-2">
                      <ButtonPrimary type="submit">Add aplication</ButtonPrimary>
                    </div>
                  </div>
                </form>
              </div>
            </CommonLayout>
          )
        }
      })()}

    </div>
  );
};

export default AccountHostRegistration;
