import { createSlice } from '@reduxjs/toolkit';

const profileSlice = createSlice({
    name: 'profile',
    initialState: {
        profile: {}
    },
    reducers: {
        setProfile: (state, action) => {
            state.profile = action.payload;
        },
        clearProfile: (state) => {
            state.profile = {};
        },
        reloadProfile: (state, action) => {
            state.profile['profile'] = action.payload;
        },
        addLikedProfile: (state, action) => {
            state.profile['favorites'].push(action.payload);
        },
        removeLikedProfile: (state, action) => {
            state.profile['favorites'] = state.profile['favorites'].filter((item) => item.tour != action.payload);
        },
        addCommentProfile: (state, action) => {
            state.profile['comments'].push(action.payload);
        }
    }
});

export const { setProfile, clearProfile, reloadProfile, addLikedProfile, removeLikedProfile, addCommentProfile } = profileSlice.actions;
export default profileSlice.reducer;

export const selectCurrentProfile = (state) => state.profile.profile;