import Heading2 from "components/Heading/Heading2";
import TourCard from "components/TourCard/TourCard";
import { Tour } from "data/types";
import { FC } from "react";

export interface SectionGridFilterCardsProps {
    className?: string;
    data?: Tour[];
    heading?: string;
    useBACK_END_URL?: boolean;
}

const SectionGridFilterCards: FC<SectionGridFilterCardsProps> = ({
    className = "",
    data = [],
    heading = "Results",
    useBACK_END_URL = false,
}) => {

    const renderCard = (tour: Tour) => {
        return <TourCard key={tour.id} data={tour}/>
    }

    return (
        <div
            className={`nc-SectionGridFilterCard ${className}`}
            data-nc-id="SectionGridFilterCard"
        >
            <Heading2 heading={`${heading} (${data.length})`} subHeading=" " />

            {/* <div className="mb-8 lg:mb-11">
                <TabFilters />
            </div> */}
            <div className="grid grid-cols-1 gap-6 md:gap-8 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
                {data.map((tour) => renderCard(tour))}
            </div>
            {/* <div className="flex mt-16 justify-center items-center">
                <Pagination />
            </div> */}
        </div>
    );
};

export default SectionGridFilterCards;
