import { usePasswordResetMutation } from "features/authApiSlice";
import { FC, useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import { useNavigate } from "react-router-dom";
import ButtonPrimary from "shared/Button/ButtonPrimary";
import Input from "shared/Input/Input";


export interface PageResetPasswordProps {
    className?: string;
}


const PageResetPassword: FC<PageResetPasswordProps> = ({ className = "" }) => {

    const [formData, setFormData] = useState({ email: "" });
    const [isSuccess, setIsSuccess] = useState(false);
    const [errMsg, setErrMsg] = useState('');

    const navigate = useNavigate();

    const handleFormChange = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    const [passwordReset] = usePasswordResetMutation();

    const handleFormSubmit = async (e) => {
        e.preventDefault()
        try {
            passwordReset({ email: formData.email }).unwrap().then(() => {
                setIsSuccess(true);
            })
        } catch (err: any) {
            console.log(err);
            setErrMsg(err.detail)
        }
    }

    useEffect(() => {
        setErrMsg('')
    }, [formData])

    useEffect(() => {
        if (isSuccess) {
            navigate('/')
        }
    }, [isSuccess])

    return (
        <div className={`nc-PageLogin ${className}`} data-nc-id="PageLogin">
            <Helmet>
                <title>Login || Booking React Template</title>
            </Helmet>
            <div className="container mb-24 lg:mb-32">
                <h2 className="my-20 flex items-center text-3xl leading-[115%] md:text-5xl md:leading-[115%] font-semibold text-neutral-900 dark:text-neutral-100 justify-center">
                    Password reset
                </h2>
                <div className="max-w-md mx-auto space-y-6">
                    {errMsg && <div className="text-red-500 text-center" style={{ borderRadius: "16px" }}>{errMsg}</div>}
                    <form className="grid grid-cols-1 gap-6" onSubmit={handleFormSubmit}>
                        <label className="block">
                            <span className="text-neutral-800 dark:text-neutral-200">
                                Email address
                            </span>
                            <Input
                                type="email"
                                placeholder="example@example.com"
                                className="mt-1"
                                name="email"
                                value={formData.email}
                                onChange={handleFormChange}
                                autoComplete="email"
                            />
                        </label>
                        <span className="block text-center text-neutral-700 dark:text-neutral-300 text-sm">
                            You will get an email with a link to reset your password.
                        </span>
                        <ButtonPrimary type="submit">Continue</ButtonPrimary>
                    </form>
                </div>
            </div>
        </div>
    );
};

export default PageResetPassword;
