import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';
import { BASE_URL } from 'contains/contants';
import { logOut, selectCurrentToken, selectRefreshToken, setCredentials } from 'features/authSlice';


const baseQuery = fetchBaseQuery({
    baseUrl: BASE_URL,
    prepareHeaders: (headers, { getState }) => {
        //@ts-ignore
        const token = selectCurrentToken(getState());
        if (token) {
            headers.set('Authorization', `Bearer ${token}`);
        }
        return headers;
    },
});

const baseQueryWithReauth = async (args, api, extraOptions) => {
    let result = await baseQuery(args, api, extraOptions);
    //@ts-ignore
    if ((result.error?.status === 401 || result.error?.error === "TypeError: Failed to fetch") && selectRefreshToken(api.getState()) !== null) {
        const refreshResult: any = await fetch(`${BASE_URL}auth/jwt/refresh/`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({refresh: selectRefreshToken(api.getState()),}),
        }).then((res) => res.json());

        if (refreshResult.access) {
            api.dispatch(setCredentials({ ...refreshResult }));
            result = await baseQuery(args, api, extraOptions);
        } 
        else {
            api.dispatch(logOut());
        }
    }
    // else if(result.error && selectRefreshToken(api.getState()) === null && selectCurrentToken(api.getState()) === null){
    //     window.location.replace('/login');
    // }
    return result;
}

export const apiSlice = createApi({
    baseQuery: baseQueryWithReauth,
    endpoints: (builder) => ({}),
});