import { useUserActivationMutation } from "features/authApiSlice";
import { FC, useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import { useNavigate, useParams } from "react-router-dom";
import ButtonPrimary from "shared/Button/ButtonPrimary";

export interface PageSignUpProps {
  className?: string;
}

const PageConfirmSignUp: FC<PageSignUpProps> = ({ className = "" }) => {

  const {uid, token} = useParams(); 

  const [isSuccess, setIsSuccess] = useState(false);

  const navigate = useNavigate();
  
  const [signupActivation] = useUserActivationMutation();

  const handleFormSubmit = async (e) => {
    e.preventDefault()
    try {

      await signupActivation({uid, token}).unwrap();

      setIsSuccess(true);

    } catch (err: any) {
      console.log(err)
    }
  }

  useEffect(() => {
    if (isSuccess) {
      navigate('/login')
    }
  }, [isSuccess])


  return (
    <div className={`nc-PageSignUp  ${className}`} data-nc-id="PageSignUp">
      <Helmet>
        <title>Sign up Confirmation || Booking React Template</title>
      </Helmet>
      <div className="container mb-24 lg:mb-32">
        <h2 className="mt-20 flex items-center text-3xl leading-[115%] md:text-5xl md:leading-[115%] font-semibold text-neutral-900 dark:text-neutral-100 justify-center">
          Signup Confirmation
        </h2>
        <p className="mb-20 mt-10 flex items-center justify-center">
            Please click button below to activate your account
        </p>
        <div className="max-w-md mx-auto space-y-6 ">
          {/* FORM */}
          <form className="grid grid-cols-1 gap-6" onSubmit={handleFormSubmit}>  
            <ButtonPrimary type="submit">Activate account</ButtonPrimary>
          </form>
        </div>
      </div>
    </div>
  );
};

export default PageConfirmSignUp;
