import { FC, useState } from "react";
import ButtonSubmit from "../ButtonSubmit";
import LocationInput from "../LocationInput";

const StaySearchForm: FC<{}> = () => {

  const [location, setLocation] = useState("");

  const renderForm = () => {
    return (
      <form className="w-full relative mt-8 flex rounded-full shadow-xl dark:shadow-2xl bg-white dark:bg-neutral-800 justify-between">
        <LocationInput className="flex-[1.5]" fatherValue={location} setFatherValue={setLocation} placeHolder={"Tour name"}/>

        <div className={`relative flex flex-[0] z-10 items-center focus:outline-none w-full `} style={{marginLeft: "auto"}}>
          <div className="pr-2 xl:pr-4">
            <ButtonSubmit href={`/listing-stay/${location}`} />
          </div>
        </div>
      </form>
    );
  };

  return renderForm();
};

export default StaySearchForm;
