import BtnLikeIcon from "components/BtnLikeIcon/BtnLikeIcon";
import GallerySlider from "components/GallerySlider/GallerySlider";
import StartRating from "components/StartRating/StartRating";
import { Image, Profile, Tour } from "data/types";
import { addLikedProfile, removeLikedProfile } from "features/profileSlice";
import { useLikeTourMutation, useUnlikeTourMutation } from "features/toursApiSlice";
import { FC } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";

export interface TourCardProps {
  className?: string;
  data: Tour;
  size?: "default" | "small";
}

const TourCard: FC<TourCardProps> = ({
  size = "default",
  className = "",
  data,
}) => {
  const {
    id,
    name,
    type,
    address,
    price,
    rating,
    images,
  } = data;

  //@ts-ignore
  const profile: Profile = useSelector(state => state.profile.profile);

  const [likeTour] = useLikeTourMutation();
  const [unlikeTour] = useUnlikeTourMutation();
  const dispatch = useDispatch();

  const onLike = () => {
    if (!profile.favorites.some(item => item.tour === id)) {
      likeTour({ user_id: profile.id, tour: id }).unwrap().then((res) => {
        dispatch(addLikedProfile(res));
      }
      ).catch((err) => {
        console.log(err);
      }
      );
    } else {
      const likedTour = profile.favorites.find(item => item.tour === id);
      unlikeTour(likedTour.id).unwrap().then((res) => {
        dispatch(removeLikedProfile(id));
      }
      ).catch((err) => {
        console.log(err);
      }
      );
    }

  };

  const isLiked = (): boolean => {
    return profile.favorites?.some(item => item.tour === id);
  }


  function adaptImages(images: Image[]) {
    let galleryImages: string[] = [];

    if (images?.length === 0) {
      galleryImages.push("");
      return galleryImages;
    }

    galleryImages = images.reduce((result: string[], obj) => {
      if (Array.isArray(obj.tour_images)) {
        return result.concat(obj.tour_images);
      } else if (typeof obj.tour_images === 'string') {
        return result.concat(obj.tour_images);
      }
      return result;
    }, []);

    return galleryImages;
  }

  const galleryImages = adaptImages(images);

  const renderSliderGallery = () => {
    return (
      <div className="relative w-full">
        <GallerySlider
          uniqueID={`StayCard_${id}`}
          ratioClass="aspect-w-4 aspect-h-3 "
          galleryImgs={galleryImages}
          href={`/listing-stay-detail/${id}`}
        />
        {isLiked() ?
          <BtnLikeIcon className="absolute right-3 top-3 z-[1]" onClickProp={onLike} isLiked={true} />
          :
          <BtnLikeIcon className="absolute right-3 top-3 z-[1]" onClickProp={onLike} isLiked={false} />
        }
        {/* {saleOff && <SaleOffBadge className="absolute left-3 top-3" />} */}
      </div>
    );
  };

  const renderContent = () => {
    return (
      <div className={size === "default" ? "p-4 space-y-4" : "p-3 space-y-2"}>
        <div className="space-y-2">
          <span className="text-sm text-neutral-500 dark:text-neutral-400">
            {type}
          </span>
          <div className="flex items-center space-x-2">
            {/* {isAds && <Badge name="ADS" color="green" />} */}
            <h2
              className={` font-medium capitalize ${size === "default" ? "text-lg" : "text-base"
                }`}
            >
              <span className="line-clamp-1">{name}</span>
            </h2>
          </div>
          <div className="flex items-center text-neutral-500 dark:text-neutral-400 text-sm space-x-2">
            {size === "default" && (
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={1.5}
                  d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z"
                />
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={1.5}
                  d="M15 11a3 3 0 11-6 0 3 3 0 016 0z"
                />
              </svg>
            )}
            <span className="">{address}</span>
          </div>
        </div>
        <div className="w-14 border-b border-neutral-100 dark:border-neutral-800"></div>
        <div className="flex justify-between items-center">
          <span className="text-base font-semibold">
            {price}
            {` `}
            {size === "default" && (
              <span className="text-sm text-neutral-500 dark:text-neutral-400 font-normal">
                $
              </span>
            )}
          </span>
          {!!rating && (
            <StartRating point={rating} />
          )}
        </div>
      </div>
    );
  };

  return (
    <div
      className={`nc-StayCard group relative bg-white dark:bg-neutral-900 border border-neutral-100 dark:border-neutral-800 rounded-2xl overflow-hidden will-change-transform hover:shadow-xl transition-shadow ${className}`}
      data-nc-id="StayCard"
    >
      {renderSliderGallery()}
      <Link to={`/listing-stay-detail/${id}`}>{renderContent()}</Link>
    </div>
  );
};

export default TourCard;
