import BgGlassmorphism from "components/BgGlassmorphism/BgGlassmorphism";
import SectionHeroArchivePage from "components/SectionHeroArchivePage/SectionHeroArchivePage";
import { Tour } from "data/types";
import { useGetToursBySearchMutation, useGetToursMutation } from "features/toursApiSlice";
import { FC, useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import { useParams } from "react-router-dom";
import SectionGridFilterCards from "./SectionGridFilterCards";

export interface ListingStayPageProps {
  className?: string;
}

const ListingStayPage: FC<ListingStayPageProps> = ({ className = "" }) => {

  const { search } = useParams();

  const [searchTours, setSearchTours] = useState<Tour[]>([]);
  const [getToursBySearch] = useGetToursBySearchMutation();
  const [getTours] = useGetToursMutation();


  useEffect( () => {
      if(search !== undefined){
        getToursBySearch(search)
        .unwrap()
        .then((res) => {
          setSearchTours(res);
        });
      }else{
        getTours({}).unwrap().then((res: Tour[]) => {
          setSearchTours(res);
        }).catch((err) => {
          console.log(err);
        });
      }
  }, [search]);


  return (
    <div
      className={`nc-ListingStayPage relative overflow-hidden ${className}`}
      data-nc-id="ListingStayPage"
    >
      <Helmet>
        <title>Chisfis || Booking React Template</title>
      </Helmet>
      <BgGlassmorphism />

      <div className="container relative overflow-hidden">
        {/* SECTION HERO */}
        <SectionHeroArchivePage

          className="pt-10 pb-24 lg:pb-28 lg:pt-16 "
        />

        {/* SECTION */}
        {search === undefined ? <SectionGridFilterCards className="pb-24 lg:pb-28" data={searchTours} heading="All tours" useBACK_END_URL={true}/> 
        : <SectionGridFilterCards className="pb-24 lg:pb-28" data={searchTours} />}
      
        {/* <SectionGridFeatureTours /> */}

        {/* SECTION 1 */}
        {/* <div className="relative py-16">
          <BackgroundSection />
          <SectionSliderNewCategories
            heading="Explore by types of stays"
            subHeading="Explore houses based on 10 types of stays"
            categoryCardType="card5"
            itemPerRow={5}
            sliderStyle="style2"
            uniqueClassName="ListingStayMapPage"
          />
        </div> */}

        {/* SECTION */}
        {/* <SectionSubscribe2 className="py-24 lg:py-28" /> */}

        {/* SECTION */}
        {/* <div className="relative py-16 mb-24 lg:mb-28">
          <BackgroundSection className="bg-orange-50 dark:bg-black dark:bg-opacity-20 " />
          <SectionGridAuthorBox />
        </div> */}
      </div>
    </div>
  );
};

export default ListingStayPage;
