import { usePasswordResetConfirmMutation } from "features/authApiSlice";
import { FC, useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import { useNavigate, useParams } from "react-router-dom";
import ButtonPrimary from "shared/Button/ButtonPrimary";
import Input from "shared/Input/Input";


export interface PageResetPasswordConfirmProps {
  className?: string;
}


const PageResetPasswordConfirm: FC<PageResetPasswordConfirmProps> = ({ className = "" }) => {

  const { uid, token } = useParams();

  const [formData, setFormData] = useState({ password: "", confirm_password: "" });
  const [isSuccess, setIsSuccess] = useState(false);
  const [errMsg, setErrMsg] = useState('');

  const navigate = useNavigate();

  const [resetPassword] = usePasswordResetConfirmMutation();

  const handleFormChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleFormSubmit = async (e) => {
    e.preventDefault()
    if (formData.password === formData.confirm_password) {
      resetPassword({ new_password: formData.password, uid, token }).unwrap().then(() => {
        setIsSuccess(true);
      }).catch((err) => {
        setErrMsg(err.detail)
      })
    } else {
      setErrMsg('Password does not match')
    }
  }

  useEffect(() => {
    setErrMsg('')
  }, [formData])

  useEffect(() => {
    if (isSuccess) {
      navigate('/login')
    }
  }, [isSuccess])

  return (
    <div className={`nc-PageLogin ${className}`} data-nc-id="PageLogin">
      <Helmet>
        <title>Login || Booking React Template</title>
      </Helmet>
      <div className="container mb-24 lg:mb-32">
        <h2 className="my-20 flex items-center text-3xl leading-[115%] md:text-5xl md:leading-[115%] font-semibold text-neutral-900 dark:text-neutral-100 justify-center">
          Enter new password
        </h2>
        <div className="max-w-md mx-auto space-y-6">
          {errMsg && <div className="text-red-500 text-center" style={{ borderRadius: "16px" }}>{errMsg}</div>}
          <form className="grid grid-cols-1 gap-6" onSubmit={handleFormSubmit}>
            <label className="block">
              <span className="flex justify-between items-center text-neutral-800 dark:text-neutral-200">
                New password
              </span>
              <Input
                type="password"
                className="mt-1"
                onChange={handleFormChange}
                value={formData.password}
                name="password"
                autoComplete="password"
              />
            </label>
            <label className="block mb-10">
              <span className="flex justify-between items-center text-neutral-800 dark:text-neutral-200">
                New password one more time
              </span>
              <Input
                type="password"
                className="mt-1"
                onChange={handleFormChange}
                value={formData.confirm_password}
                name="confirm_password"
                autoComplete="password"
              />
            </label>
            <ButtonPrimary type="submit">Continue</ButtonPrimary>
          </form>
        </div>
      </div>
    </div>
  );
};

export default PageResetPasswordConfirm;
