import { apiSlice } from "../app/api/apiSlice";

export const profileApiSlice = apiSlice.injectEndpoints({
    endpoints: (builder) => ({
        getProfile: builder.mutation({
            query: (credentials) => {
                return ({
                    url: `account/${credentials}/`,
                    method: 'GET'
                })
            },
        }),
        getMiniProfile: builder.mutation({
            query: (credentials) => {
                return ({
                    url: `account/profile/${credentials}/`,
                    method: 'GET'
                })
            },
        }),
        putProfile: builder.mutation({
            query: ({ id, body }) => {
                return ({
                    url: `account/profile/${id}/`,
                    method: 'PUT',
                    body: body
                })
            },
        }),
        putProfileAvatar: builder.mutation({
            query: ({ avatarData, fileName }) => {
                return ({
                    url: `account/profile/image/upload/`,
                    method: 'POST',
                    headers: {
                        'Content-Disposition': `attachment; filename='${fileName}';`,
                    },
                    body: avatarData
                })
            },
        }),
        putProfilePassport: builder.mutation({
            query: ({ passportData, fileName }) => {
                return ({
                    url: `account/profile/host/image/upload/`,
                    method: 'POST',
                    headers: {
                        'Content-Disposition': `attachment; filename='${fileName}';`,
                    },
                    body: passportData
                })
            }
        }),
        putProfilePhone: builder.mutation({
            query: (credentials) => {
                return ({
                    url: `account/profile/host/${credentials.user_id}/ `,
                    method: 'PUT',
                    body: credentials
                })
            }
        }),
        putProfilePassword: builder.mutation({
            query: (credentials) => {
                return ({
                    url: `auth/users/set_password/`,
                    method: 'POST',
                    body: credentials
                })
            }
        }),
        putProfileSocials: builder.mutation({
            query: ({id, formData}) => {
                return ({
                    url: `account/profile/social/${id}/`,
                    method: 'PUT',
                    body: formData
                })
            }
        }),
    }),
});

export const { useGetProfileMutation, useGetMiniProfileMutation, usePutProfileMutation, usePutProfileAvatarMutation, usePutProfilePassportMutation, usePutProfilePhoneMutation, usePutProfilePasswordMutation, usePutProfileSocialsMutation } = profileApiSlice
