import FooterNav from "components/FooterNav";
import AccountBilling from "containers/AccountPage/AccountBilling";
import AccountHostRegistration from "containers/AccountPage/AccountHostRegistration";
import AccountPage from "containers/AccountPage/AccountPage";
import AccountPass from "containers/AccountPage/AccountPass";
import AccountSavelists from "containers/AccountPage/AccountSavelists";
import AuthorIdPage from "containers/AuthorPage/AuthorIdPage";
import AuthorPage from "containers/AuthorPage/AuthorPage";
import ListingStayDetailPage from "containers/ListingDetailPage/listing-stay-detail/ListingStayDetailPage";
import ListingCityPage from "containers/ListingStayPage/ListingCityPage";
import ListingStayPage from "containers/ListingStayPage/ListingStayPage";
import Page404 from "containers/Page404/Page404";
import PageAbout from "containers/PageAbout/PageAbout";
import PageAddListing1 from "containers/PageAddListing1/PageAddListing1";
import PageAddListing10 from "containers/PageAddListing1/PageAddListing10";
import PageAddListing7 from "containers/PageAddListing1/PageAddListing7";
import PageContact from "containers/PageContact/PageContact";
import PageHome from "containers/PageHome/PageHome";
import PageLogin from "containers/PageLogin/PageLogin";
import PageSignUp from "containers/PageSignUp/PageSignUp";
import RequireAuth from "containers/RequireAuth";
import RequireHost from "containers/RequireHost";
import SiteHeader from "containers/SiteHeader";
import useWindowSize from "hooks/useWindowResize";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import Footer from "shared/Footer/Footer";
import ScrollToTop from "./ScrollToTop";
import { Page } from "./types";
import PageConfirmSignUp from "containers/PageSignUp/PageConfirmSignUp";
import PageResetPassword from "containers/PageLogin/PageResetPassword";
import PageResetPasswordConfirm from "containers/PageLogin/PageResetPasswordConfirm";

export const pages: Page[] = [
  { path: "/", exact: true, component: PageHome },
  { path: "/#", exact: true, component: PageHome },
  //
  { path: "/listing-stay", component: ListingStayPage },
  { path: "/listing-stay/:search", component: ListingStayPage },
  { path: "/listing-city/:city", component: ListingCityPage },
  { path: "/listing-stay-detail/:id", component: ListingStayDetailPage },
  //
  { path: "/author/:id", component: AuthorIdPage },
  //
  { path: "/contact", component: PageContact },
  { path: "/about", component: PageAbout },
  //
  { path: "/signup", component: PageSignUp },
  { path: "/login", component: PageLogin },
  { path: "/reset-pass", component: PageResetPassword },
  { path: "/reset-pass-confirm/:uid/:token", component: PageResetPasswordConfirm },
  { path: "/signup-confirm/:uid/:token", component: PageConfirmSignUp },
  //
];

export const protectedAuthPages: Page[] = [
  { path: "/author", component: AuthorPage },
  { path: "/account", component: AccountPage },
  { path: "/account-host-registration", component: AccountHostRegistration },
  { path: "/account-password", component: AccountPass },
  { path: "/account-savelists", component: AccountSavelists },
  { path: "/account-billing", component: AccountBilling },
]

export const protectedHostPages: Page[] = [
  { path: "/add-listing-1", component: PageAddListing1 },
  { path: "/add-listing-7", component: PageAddListing7 },
  { path: "/add-listing-10", component: PageAddListing10 },
]

const MyRoutes = () => {
  let WIN_WIDTH = useWindowSize().width;
  if (typeof window !== "undefined") {
    WIN_WIDTH = WIN_WIDTH || window.innerWidth;
  }

  return (
    <BrowserRouter>
      <ScrollToTop />
      <SiteHeader />

      <Routes>
        {pages.map(({ component, path }) => {
          const Component = component;
          return <Route key={path} element={<Component />} path={path} />;
        })}
        <Route element={<RequireAuth />}>
          {protectedAuthPages.map(({ component, path }) => {
            const Component = component;
            return <Route key={path} element={<Component />} path={path} />;
          })}
        </Route>
        <Route element={<RequireHost />}>
          {protectedHostPages.map(({ component, path }) => {
            const Component = component;
            return <Route key={path} element={<Component />} path={path} />;
          })}
        </Route>
        <Route element={<Page404 />} />
      </Routes>

      {WIN_WIDTH < 768 && <FooterNav />}
      <Footer />
    </BrowserRouter>
  );
};

export default MyRoutes;
