import Label from "components/Label/Label";
import { Profile } from "data/types";
import { usePutProfileAvatarMutation, usePutProfileMutation, usePutProfileSocialsMutation } from "features/profileApiSlice";
import { reloadProfile } from "features/profileSlice";
import { FC, useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import { useDispatch, useSelector } from "react-redux";
import Avatar from "shared/Avatar/Avatar";
import ButtonPrimary from "shared/Button/ButtonPrimary";
import Input from "shared/Input/Input";
import Select from "shared/Select/Select";
import Textarea from "shared/Textarea/Textarea";
import formatDate from "utils/transformDateToInput";
import CommonLayout from "./CommonLayout";

export interface AccountPageProps {
  className?: string;
}

interface FormData {
  user: number;
  name: string;
  city: string;
  sex: string;
  date_birth: Date;
  address: string;
  bio: string;
  facebook: string;
  linked_in: string;
  youtube: string;
  instagram: string;
}

const AccountPage: FC<AccountPageProps> = ({ className = "" }) => {

  //@ts-ignore
  const profile: Profile = useSelector(state => state.profile.profile);

  const initFormData: FormData = {
    user: profile?.id,
    name: profile?.profile?.name || "",
    city: profile?.profile?.city || "",
    sex: profile.profile?.sex || "Other",
    date_birth: profile.profile ? new Date(profile.profile.date_birth) : new Date(),
    address: profile.profile?.address || "",
    bio: profile.profile?.bio || "",
    facebook: profile.profile?.socials[0].facebook || "",
    linked_in: profile.profile?.socials[0].linked_in || "",
    youtube: profile.profile?.socials[0].youtube || "",
    instagram: profile.profile?.socials[0].instagram || "",
  }

  const [formData, setFormData] = useState(initFormData);
  const [successMsg, setSuccessMsg] = useState('');
  const [errMsg, setErrMsg] = useState('');

  const dispatch = useDispatch();

  const [editProfile] = usePutProfileMutation();
  const [editAvatar] = usePutProfileAvatarMutation();
  const [editSocials] = usePutProfileSocialsMutation();

  const handleFormChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleDateChange = (e) => {
    setFormData({ ...formData, date_birth: new Date(e.target.value) });
  };

  const handleFormSubmit = async (event) => {
    event.preventDefault();

    if (event.target[0].files[0]) {
      const avatar = event.target[0].files[0] as File;
      const avatarData = new FormData();

      avatarData.append('avatar', avatar, avatar.name);
      avatarData.append('user_id', String(profile.id));

      await editAvatar({ fileName: avatar.name, avatarData }).unwrap();
    }

    await editSocials({ id: profile.id, formData: {...formData, user_profile_id: profile.id} }).unwrap().catch((err) => {
      console.log(err);
    });

    editProfile({ id: profile.id, body: formData })
      .unwrap()
      .then((res) => {
        dispatch(reloadProfile(res));
        setSuccessMsg('Update Success');
      })
      .catch((err) => {
        if (err.status == 400) {
          setErrMsg('One of felids is invalid');
        } else if (err.originalStatus === 401) {
          setErrMsg('Unauthorized');
        } else if (!err?.originalStatus) {
          setErrMsg('No Server Response');
        } else {
          setErrMsg('Something went wrong');
        }
      })
  }

  const [uploadedFiles, setUploadedFiles] = useState<number>(0);

  const handleFileUploading = (e) => {
    const files = e.target.files;
    const allFiles = Array.from(files);
    setUploadedFiles(allFiles.length);
  }

  useEffect(() => {
    setErrMsg('');
    setSuccessMsg('');
  }, [formData])

  return (
    <div className={`nc-AccountPage ${className}`} data-nc-id="AccountPage">
      <Helmet>
        <title>Account || Booking React Template</title>
      </Helmet>
      <CommonLayout>
        <div className="space-y-6 sm:space-y-8">
          {/* HEADING */}
          <h2 className="text-3xl font-semibold">Account infomation</h2>
          <div className="w-14 border-b border-neutral-200 dark:border-neutral-700"></div>
          {errMsg && <div className="text-red-500 text-center" style={{ borderRadius: "16px" }}>{errMsg}</div>}
          {successMsg && <div className="text-green-500 text-center" style={{ borderRadius: "16px" }}>{successMsg}</div>}
          <form className="flex flex-col md:flex-row" onSubmit={handleFormSubmit}>
            <div className="flex-shrink-0 flex items-start">
              <div className="relative rounded-full overflow-hidden flex">
                {profile.profile?.profile_images.at(-1) ?
                  <Avatar sizeClass="w-64 h-64" imgUrl={profile.profile.profile_images.at(-1).avatar} />
                  :
                  <Avatar sizeClass="w-64 h-64" />
                }
                <div className="absolute inset-0 bg-black bg-opacity-60 flex flex-col items-center justify-center text-neutral-50 cursor-pointer">
                  <svg
                    width="30"
                    height="30"
                    viewBox="0 0 30 30"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M17.5 5H7.5C6.83696 5 6.20107 5.26339 5.73223 5.73223C5.26339 6.20107 5 6.83696 5 7.5V20M5 20V22.5C5 23.163 5.26339 23.7989 5.73223 24.2678C6.20107 24.7366 6.83696 25 7.5 25H22.5C23.163 25 23.7989 24.7366 24.2678 24.2678C24.7366 23.7989 25 23.163 25 22.5V17.5M5 20L10.7325 14.2675C11.2013 13.7988 11.8371 13.5355 12.5 13.5355C13.1629 13.5355 13.7987 13.7988 14.2675 14.2675L17.5 17.5M25 12.5V17.5M25 17.5L23.0175 15.5175C22.5487 15.0488 21.9129 14.7855 21.25 14.7855C20.5871 14.7855 19.9513 15.0488 19.4825 15.5175L17.5 17.5M17.5 17.5L20 20M22.5 5H27.5M25 2.5V7.5M17.5 10H17.5125"
                      stroke="currentColor"
                      strokeWidth={1.5}
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                  </svg>

                  {!uploadedFiles && <span className="mt-1 text-xs">Change Image</span>}
                  {uploadedFiles !== 0 && <span className="text-center text-sm">You uploaded a new avatar. <br/>You need update info</span>}
                </div>
                <input
                  type="file"
                  className="absolute inset-0 opacity-0 cursor-pointer"
                  name="avatar"
                  accept="image/png, image/jpeg, image/jpg, image/webp"
                  onChange={handleFileUploading}
                // accept="image/png, image/jpeg"
                />
              </div>
            </div>
            <div className="flex-grow mt-10 md:mt-0 md:pl-16 max-w-3xl space-y-6" >
              <div>
                <Label>Name</Label>
                <Input
                  className="mt-1.5"
                  // defaultValue="Eden Tuan"
                  name="name"
                  value={formData.name}
                  onChange={handleFormChange}
                  required
                />
              </div>
              {/* ---- */}
              <div>
                <Label>Gender</Label>
                <Select className="mt-1.5" name="sex" value={formData.sex} onChange={handleFormChange} required>
                  <option value="Male">Male</option>
                  <option value="Female">Female</option>
                  <option value="Other">Other</option>
                </Select>
              </div>
              {/* ---- */}
              <div>
                <Label>City</Label>
                <Input
                  className="mt-1.5"
                  //defaultValue="example@email.com"
                  name="city"
                  value={formData.city}
                  onChange={handleFormChange}
                  required
                />
              </div>
              {/* ---- */}
              <div className="max-w-lg">
                <Label>Date of birth</Label>
                <Input
                  className="mt-1.5"
                  type="date"
                  //defaultValue="1990-07-22"
                  name="dateOfBirth"
                  value={formatDate(formData.date_birth)}
                  onChange={handleDateChange}
                />
              </div>
              {/* ---- */}
              <div>
                <Label>Addess</Label>
                <Input
                  required
                  className="mt-1.5"
                  //defaultValue="New york, USA"
                  name="address"
                  value={formData.address}
                  onChange={handleFormChange}
                />
              </div>
              {/* ---- */}
              <div>
                <Label>About you</Label>
                <Textarea
                  className="mt-1.5"
                  //defaultValue="..."
                  name="bio"
                  value={formData.bio}
                  onChange={handleFormChange}
                />
              </div>
              <div>
                <Label>Facebook</Label>
                <Input
                  className="mt-1.5"
                  //defaultValue="New york, USA"
                  name="facebook"
                  value={formData.facebook}
                  onChange={handleFormChange}
                />
              </div>
              <div>
                <Label>Linked in</Label>
                <Input
                  className="mt-1.5"
                  //defaultValue="New york, USA"
                  name="linked_in"
                  value={formData.linked_in}
                  onChange={handleFormChange}
                />
              </div>
              <div>
                <Label>Youtube</Label>
                <Input
                  className="mt-1.5"
                  //defaultValue="New york, USA"
                  name="youtube"
                  value={formData.youtube}
                  onChange={handleFormChange}
                />
              </div>
              <div>
                <Label>Instagram</Label>
                <Input
                  className="mt-1.5"
                  //defaultValue="New york, USA"
                  name="instagram"
                  value={formData.instagram}
                  onChange={handleFormChange}
                />
              </div>
              <div className="pt-2">
                <ButtonPrimary type="submit">Update info</ButtonPrimary>
              </div>
            </div>
          </form>
        </div>
      </CommonLayout>
    </div>
  );
};

export default AccountPage;
