import LocationMarker from "components/AnyReactComponent/LocationMarker";
import Label from "components/Label/Label";
import { useGetTourCitysMutation, usePutTourMutation } from "features/toursApiSlice";
import GoogleMapReact from "google-map-react";
import { FC, useEffect, useState } from "react";
import { useSelector } from "react-redux";
import ButtonPrimary from "shared/Button/ButtonPrimary";
import Input from "shared/Input/Input";
import Select from "shared/Select/Select";
import Textarea from "shared/Textarea/Textarea";
import CommonLayout from "./CommonLayout";
import FormItem from "./FormItem";
import { BASE_URL } from '../../contains/contants'

export interface PageAddListing1Props { }
export interface OptionsProps {}

const PageAddListing1: FC<PageAddListing1Props> = () => {
  const [options, setOptions] = useState([])

  //@ts-ignore
  const profile = useSelector(state => state.profile.profile);

  const getTypesTour = async () => {
    try {
      const response = await fetch(`${BASE_URL}tours/types/`)
      const data = await response.json()
      console.log(data);
      setOptions(data)
    }
    catch (error) {
      console.log(error);
    }
  }

  

  const [successMsg, setSuccessMsg] = useState('');
  const [errMsg, setErrMsg] = useState('');
  const [citys, setCitys] = useState([]);
  const [formData, setFormData] = useState({
    type: 1,
    name: "",
    price: 0,
    city: 1,
    user_id: profile.id,
    date_start: "",
    date_finish: "",
    description: "",
    languages: "English",
    longitude: 55.9607277,
    latitude: 36.2172614,
    address: "",
  });

  const [createTour] = usePutTourMutation();
  const [getCitys] = useGetTourCitysMutation();

  useEffect(() => {
    getCitys({}).unwrap().then((res) => {
      setCitys(res);
    })
    .catch((err) => {
      console.log(err);
    })
    getTypesTour()
  }, [])

  const handleFormChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleMapChange = (e) => {
    setFormData({ ...formData, longitude: e.lng, latitude: e.lat });
  };

  const handleFormSubmit = async (e) => {
    e.preventDefault();
    try {
      const tour = await createTour(formData).unwrap();
      sessionStorage.setItem('created-tour-id', tour.id);
      setSuccessMsg('Tour created successfully');
    } catch (error: any) {
      if (error.status == 400) {
        setErrMsg('One of felids is invalid');
      } else if (error.originalStatus === 401) {
        setErrMsg('Unauthorized');
      } else if (!error?.originalStatus) {
        setErrMsg('No Server Response');
      } else {
        setErrMsg('Something went wrong');
      }
    }
  };

  return (
    <CommonLayout
      index="1"
      backtHref="/"
      nextHref="/add-listing-7"
      nextBtnText="Add photos"
    >
      <form onSubmit={handleFormSubmit}>
        <h2 className="text-2xl font-semibold">Choosing listing categories</h2>
        {/* <div className="w-14 border-b border-neutral-200 dark:border-neutral-700"></div> */}
        {/* FORM */}
        <div className="space-y-8">
          {/* ITEM */}
          <FormItem
            label="Place name"
            desc="A catchy name usually includes: House name + Room name + Featured property + Tourist destination"
          >
            <Input placeholder="Places name" name="name" value={formData.name} onChange={handleFormChange} required/>
          </FormItem>
          <FormItem
            label="Choose a property type"
            desc="Hotel: Professional hospitality businesses that usually have a unique style or theme defining their brand and decor"
          >
            <Select name="type" onChange={handleFormChange} required>
              <option value="Select type" disabled>Select type</option>
              {/* <option value="Backpaking">Backpaking</option>
              <option value="Weekend">Weekend</option>
              <option value="Adventure">Adventure</option>
              <option value="Solo">Solo</option> */}
              {options.map((opt:any) => <option key={opt.id} value={opt.id}>{opt.type}</option>)}
            </Select>
          </FormItem>
          <FormItem label="Language">
            <Select value={formData.languages} name="languages" onChange={handleFormChange} required>
              <option value="English">English</option>
              <option value="Ukrainian">Ukrainian</option>
              <option value="Spanish">Spanish</option>
              <option value="Frenc">French</option>
              <option value="German">German</option>
            </Select>
          </FormItem>
          <div>
            <h2 className="text-2xl font-semibold">Price your space</h2>
            <span className="block mt-2 text-neutral-500 dark:text-neutral-400">
              The host's revenue is directly dependent on the setting of rates and
              regulations on the number of guests, the number of nights, and the
              cancellation policy.
            </span>
          </div>
          <div className="w-14 border-b border-neutral-200 dark:border-neutral-700"></div>
          {/* FORM */}
          <div className="space-y-8">
            {/* ITEM */}
            <FormItem label="Set Price">
              <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <span className="text-gray-500">$</span>
                </div>
                <Input className="!pl-8 !pr-10" placeholder="0.00" value={formData.price} name="price" onChange={handleFormChange} required/>
                <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                  <span className="text-gray-500">USD</span>
                </div>
              </div>
            </FormItem>
          </div>
          <h2 className="text-2xl font-semibold">Your place location</h2>
          <div className="w-14 border-b border-neutral-200 dark:border-neutral-700"></div>
          {/* <ButtonSecondary>
            <MapPinIcon className="w-5 h-5 text-neutral-500 dark:text-neutral-400" />
            <span className="ml-3">Use current location</span>
          </ButtonSecondary> */}
          {/* ITEM */}
          <FormItem label="City">
            <Select value={formData.city} name="city" onChange={handleFormChange} required>
              {citys.map((city: any) =>
                <option value={city?.id}>{city?.city_name}</option>
              )}
            </Select>
          </FormItem>
          <FormItem label="Location">
            <Input placeholder="..." value={formData.address} name="address" onChange={handleFormChange} required/>
          </FormItem>
          <div>
            <Label>Detailed address</Label>
            <span className="block mt-1 text-sm text-neutral-500 dark:text-neutral-400">
              1110 Pennsylvania Avenue NW, Washington, DC 20230
            </span>
            <div className="mt-4">
              <div className="aspect-w-5 aspect-h-5 sm:aspect-h-3">
                <div className="rounded-xl overflow-hidden">
                  <GoogleMapReact
                    bootstrapURLKeys={{
                      key: "AIzaSyDrSnyXWd7Dew-YjFK6EW9euEMAc0hEMnI",
                    }}
                    onClick={handleMapChange}
                    yesIWantToUseGoogleMapApiInternals
                    defaultZoom={15}
                    defaultCenter={{
                      lat: 55.9607277,
                      lng: 36.2172614,
                    }}
                  >
                    <LocationMarker lat={formData.latitude} lng={formData.longitude} />
                  </GoogleMapReact>
                </div>
              </div>
            </div>
          </div>
          <h2 className="text-2xl font-semibold">Your place date</h2>
          <div className="w-14 border-b border-neutral-200 dark:border-neutral-700"></div>
          <FormItem label="Start date">
            <Input type="date" value={formData.date_start} name="date_start" onChange={handleFormChange} required/>
          </FormItem>
          <FormItem label="End date">
            <Input type="date" value={formData.date_finish} name="date_finish" onChange={handleFormChange} required/>
          </FormItem>
          <div>
            <h2 className="text-2xl font-semibold">
              Your place description for client
            </h2>
            <span className="block mt-2 text-neutral-500 dark:text-neutral-400">
              Mention the best features of your accommodation, any special
              amenities like fast Wi-Fi or parking, as well as things you like
              about the neighborhood.
            </span>
          </div>

          <Textarea placeholder="Text..." rows={14} name="description" value={formData.description} onChange={handleFormChange} required/>

        </div>
        <ButtonPrimary type="submit" className="mt-10 w-full">Create</ButtonPrimary>
        {errMsg && <div className="text-red-500 text-center mt-5" style={{ borderRadius: "16px" }}>{errMsg}</div>}
        {successMsg && <div className="text-green-500 text-center mt-5" style={{ borderRadius: "16px" }}>{successMsg}</div>}
      </form>
    </CommonLayout>
  );
};

export default PageAddListing1;
