import Label from "components/Label/Label";
import React, { useState } from "react";
import ButtonPrimary from "shared/Button/ButtonPrimary";
import Input from "shared/Input/Input";
import CommonLayout from "./CommonLayout";
import { usePutProfilePasswordMutation } from "features/profileApiSlice";

const AccountPass = () => {

  const [refreshPassword] = usePutProfilePasswordMutation();
  const [successMsg, setSuccessMsg] = useState('');
  const [errMsg, setErrMsg] = useState('');

  const [formData, setFormData] = useState({
    current_password: "",
    new_password_1: "",
    new_password: "",
  });

  const handleFormChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleChange = (e: any) => {
    e.preventDefault();
    if (formData.new_password_1 == formData.new_password) {
      refreshPassword({ current_password: formData.current_password, new_password: formData.new_password }).unwrap().then((res) => {
        setSuccessMsg('Password updated successfully!')
      }).catch((err) => {
        console.log(err);
        setErrMsg(err.data.detail)
      })
    }
  };

  return (
    <div>
      <CommonLayout>
        <form className="space-y-6 sm:space-y-8" onSubmit={handleChange}>
          {/* HEADING */}
          <h2 className="text-3xl font-semibold">Update your password</h2>
          <div className="w-14 border-b border-neutral-200 dark:border-neutral-700"></div>
          <div className=" max-w-xl space-y-6">
            <div>
              <Label>Current password</Label>
              <Input type="password" className="mt-1.5" value={formData.current_password} name="current_password" onChange={handleFormChange} />
            </div>
            <div>
              <Label>New password</Label>
              <Input type="password" className="mt-1.5" value={formData.new_password_1} name="new_password_1" onChange={handleFormChange} />
            </div>
            <div>
              <Label>Confirm password</Label>
              <Input type="password" className="mt-1.5" value={formData.new_password} name="new_password" onChange={handleFormChange} />
            </div>
            <div className="pt-2">
              <ButtonPrimary type="submit">Update password</ButtonPrimary>
            </div>
          </div>
        </form>
        {errMsg && <div className="text-red-500 text-center mt-5" style={{ borderRadius: "16px" }}>{errMsg}</div>}
        {successMsg && <div className="text-green-500 text-center mt-5" style={{ borderRadius: "16px" }}>{successMsg}</div>}
      </CommonLayout>
    </div>
  );
};

export default AccountPass;
