  const onShare = () => {
    var copyText = window.location.href;

    navigator.clipboard.writeText(copyText);

    let popup = document.getElementById('copied');
    if (popup != null) {
      popup.style.display = 'block';
      setTimeout(function () {
        if (popup != null)
          popup.style.display = 'none';
      }, 1000);
    }
  };

