import CommentListing from "components/CommentListing/CommentListing";
import TourCard from "components/TourCard/TourCard";
import { Profile, Tour } from "data/types";
import { useGetProfileMutation } from "features/profileApiSlice";
import { FC, ReactNode, useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import { useSelector } from 'react-redux';
import { useParams } from "react-router-dom";
import Avatar from "shared/Avatar/Avatar";
import ButtonSecondary from "shared/Button/ButtonSecondary";
import SocialsList from "shared/SocialsList/SocialsList";
import transformDateFormat from "utils/transformDateFormat";

export interface AuthorPageProps {
  className?: string;
}

const initialState = {
  id: 0,
  email: "",
  date_joined: "",
  profile: null,
  tours: [],
  favorites: [],
  comments: [],
  host: null,
}

const AuthorIdPage: FC<AuthorPageProps> = ({ className = "" }) => {
  //@ts-ignore
  const yourState = useSelector(state => state.auth.tokens);

  const [accountInfo, setAccountInfo] = useState<Profile>(initialState);

  const [getProfile] = useGetProfileMutation();

  const { id } = useParams();

  useEffect(() => {
    try {
      getProfile(id).unwrap().then((res: Profile) => {
        setAccountInfo(res);
      })
    } catch (error) {
      console.log(error);
    }

  }, [yourState]);

  const renderCard = (tour: Tour) => {
    return <TourCard key={tour.id} data={tour} />;
  };

  const renderSidebar = () => {
    return (
      <div className=" w-full flex flex-col items-center text-center sm:rounded-2xl sm:border border-neutral-200 dark:border-neutral-700 space-y-6 sm:space-y-7 px-0 sm:p-6 xl:p-8">
        {
        }

        {function (): ReactNode {

          const hasProfileImage = accountInfo.profile?.profile_images.length;
          const isHost = accountInfo.host?.is_host;
          const imgUrl = accountInfo?.profile?.profile_images.at(-1)?.avatar;


          if (!hasProfileImage) {
            if (isHost) {
              return <Avatar hasChecked hasCheckedClass="w-6 h-6 -top-0.5 right-2" sizeClass="w-28 h-28" />
            } else {
              return <Avatar hasCheckedClass="w-6 h-6 -top-0.5 right-2" sizeClass="w-28 h-28" />
            }
          } else {
            if (isHost) {
              return <Avatar hasChecked hasCheckedClass="w-6 h-6 -top-0.5 right-2" sizeClass="w-28 h-28" imgUrl={imgUrl} />
            } else {
              return <Avatar hasCheckedClass="w-6 h-6 -top-0.5 right-2" sizeClass="w-28 h-28" imgUrl={imgUrl} />
            }
          }
        }()}


        {/* ---- */}
        <div className="space-y-3 text-center flex flex-col items-center">
          <h2 className="text-3xl font-semibold">{accountInfo.profile?.name}</h2>
          {/* <StartRating className="!text-base" /> */}
        </div>

        {/* ---- */}
        <p className="text-neutral-500 dark:text-neutral-400">
          {accountInfo.profile?.bio}
        </p>

        {/* ---- */}
        <SocialsList
          className="!space-x-3"
          itemClass="flex items-center justify-center w-9 h-9 rounded-full bg-neutral-100 dark:bg-neutral-800 text-xl"
          socials={accountInfo.profile?.socials[0]}
        />

        {/* ---- */}
        <div className="border-b border-neutral-200 dark:border-neutral-700 w-14"></div>

        {/* ---- */}
        <div className="space-y-4">
          {accountInfo.profile?.city && <div className="flex items-center space-x-4">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6 text-neutral-400"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={1.5}
                d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6"
              />
            </svg>
            <span className="text-neutral-6000 dark:text-neutral-300">
              {accountInfo.profile?.city}
            </span>
          </div>
          }
          {/* <div className="flex items-center space-x-4">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6 text-neutral-400"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={1.5}
                d="M7 8h10M7 12h4m1 8l-4-4H5a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v8a2 2 0 01-2 2h-3l-4 4z"
              />
            </svg>
            <span className="text-neutral-6000 dark:text-neutral-300">
              Speaking English
            </span>
          </div> */}
          {accountInfo.host?.telephone && <div className="flex items-center space-x-4">
            <i className="las la-phone" style={{ fontSize: "23px", color: "rgb(156, 163, 175)" }} ></i>
            <span className="text-neutral-6000 dark:text-neutral-300">
              {accountInfo.host?.telephone}
            </span>
          </div>}

          <div className="flex items-center space-x-4">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6 text-neutral-400"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={1.5}
                d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z"
              />
            </svg>
            <span className="text-neutral-6000 dark:text-neutral-300">
              {transformDateFormat(accountInfo.date_joined)}
            </span>
          </div>
        </div>
      </div>
    );
  };

  const renderSection1 = () => {
    return (
      <div className="listingSection__wrap">
        <div>
          <h2 className="text-2xl font-semibold">{accountInfo.profile?.name}'s listings</h2>
          <span className="block mt-2 text-neutral-500 dark:text-neutral-400">
            {accountInfo.profile?.name}'s listings is very rich, 5 star reviews help him to be
            more branded.
          </span>
        </div>
        <div>
          <div className="mt-8 grid grid-cols-1 gap-6 md:gap-7 sm:grid-cols-2">
            {accountInfo?.host?.tours.map(tour => renderCard(tour))}
          </div>
          {/* <div className="flex mt-11 justify-center items-center">
            <ButtonSecondary>Show me more</ButtonSecondary>
          </div> */}
        </div>
      </div>
    );
  };

  const renderSection2 = () => {
    return (
      <div className="listingSection__wrap">
        {/* HEADING */}
        <h2 className="text-2xl font-semibold">Reviews ({accountInfo?.comments.length})</h2>

        {/* comment */}
        <div className="divide-y divide-neutral-100 dark:divide-neutral-800">
          {accountInfo?.comments && accountInfo?.comments.map((item) => (
            <CommentListing className="py-8" data={item} key={item.id} />
          ))}
          {accountInfo?.comments.length > 0 &&
            <div className="pt-8">
              <ButtonSecondary>View more 20 reviews</ButtonSecondary>
            </div>
          }
          {accountInfo?.comments.length == 0 && <div className="pt-8">
            <span className="text-neutral-500 dark:text-neutral-400">
              No reviews yet
            </span>
          </div>}
        </div>
      </div>
    );
  };

  return (
    <div className={`nc-AuthorPage ${className}`} data-nc-id="AuthorPage">
      <Helmet>
        <title>Login || Booking React Template</title>
      </Helmet>
      <main className="container mt-12 mb-24 lg:mb-32 flex flex-col lg:flex-row">
        <div className="block flex-grow mb-24 lg:mb-0">
          <div className="lg:sticky lg:top-24">{renderSidebar()}</div>
        </div>
        <div className="w-full lg:w-3/5 xl:w-2/3 space-y-8 lg:space-y-10 lg:pl-10 flex-shrink-0">
          {renderSection1()}
          {renderSection2()}
        </div>
      </main>
    </div>
  );
};

export default AuthorIdPage;
