import { apiSlice } from "../app/api/apiSlice";

export const tourApiSlice = apiSlice.injectEndpoints({
    endpoints: (builder) => ({
        getTours: builder.mutation({
            query: (credentials) => {
                return ({
                    url: `tours/`,
                    method: 'GET'
                })
            },
        }),
        getTourById: builder.mutation({
            query: (credentials) => {
                return ({
                    url: `tours/${credentials}/`,
                    method: 'GET'
                })
            },
        }),
        putTour: builder.mutation({
            query: (credentials) => {
                return ({
                    url: `tours/create/`,
                    method: 'POST',
                    body: credentials
                })
            },
        }),
        likeTour: builder.mutation({
            query: (credentials) => {
                return ({
                    url: `tours/add_to_favorites/`,
                    method: 'POST',
                    body: credentials
                })
            }
        }),
        unlikeTour: builder.mutation({
            query: (credentials) => {
                return ({
                    url: `tours/delete_favorite/${credentials}/`,
                    method: 'DELETE',
                })
            }
        }),
        reviewTour: builder.mutation({
            query: (credentials) => {
                return ({
                    url: `tours/comment/create/`,
                    method: 'POST',
                    body: credentials
                })
            }
        }),
        getTourCitys: builder.mutation({
            query: () => {
                return ({
                    url: `tours/cities/`,
                    method: 'GET',
                })
            }
        }),
        putTourImages: builder.mutation({
            query: ({ imagesData, fileName }) => {
                return ({
                    url: `tours/images/upload/`,
                    method: 'POST',
                    headers: {
                        'Content-Disposition': `attachment; filename='${fileName}';`,
                    },
                    body: imagesData
                })
            }
        }),
        getToursBySearch: builder.mutation({
            query: (credentials) => {
                return ({
                    url: `tours/search/?search=${credentials}`,
                    method: 'GET',
                })
            }
        }),
        getToursByType: builder.mutation({
            query: (credentials) => {
                return ({
                    url: `tours/search?search=${credentials}`,
                    method: 'GET',
                })
            }
        }),
    }),
});

export const { useGetToursMutation, useGetTourByIdMutation, usePutTourMutation, useLikeTourMutation, useUnlikeTourMutation, useReviewTourMutation, useGetTourCitysMutation, usePutTourImagesMutation, useGetToursBySearchMutation, useGetToursByTypeMutation } = tourApiSlice
