import TourCard from "components/TourCard/TourCard";
import { StayDataType, Tour } from "data/types";
import { useGetToursByTypeMutation, useGetToursMutation } from "features/toursApiSlice";
import { FC, ReactNode, useEffect, useState } from "react";
import HeaderFilter from "./HeaderFilter";


export interface SectionGridFeatureToursProps {
  stayListings?: StayDataType[];
  gridClass?: string;
  heading?: ReactNode;
  subHeading?: ReactNode;
  headingIsCenter?: boolean;
  tabs?: string[];
}

const SectionGridFeatureTours: FC<SectionGridFeatureToursProps> = ({
  gridClass = "",
  heading = "Featured tours",
  subHeading = "Popular tours to visit based on rating",
  headingIsCenter,
  tabs = [],
}) => {


  const renderCard = (tour: Tour) => {
    return <TourCard key={tour.id} data={tour} />;
    
  };

  const [getTours] = useGetToursMutation();
  const [getToursByType] = useGetToursByTypeMutation();
  const [allTours, setAllTours] = useState([] as Tour[]);
  const [activeTab, setActiveTab] = useState(tabs[0]);

  useEffect(() => {

    if(tabs.length === 0){
      getTours({}).unwrap().then((res: Tour[]) => {
        const sortedTours: Tour[] = [...res].sort((a, b) => b.rating - a.rating);
        setAllTours(sortedTours);
      }).catch((err) => {
        console.log(err);
      });
    }else{
      getToursByType(activeTab).unwrap().then((res: Tour[]) => {
        setAllTours(res);
      }).catch((err) => {
        console.log(err);
      });
    }
  }, [activeTab]);

  const handleClickTab = (item: string) => {
    setActiveTab(item);
  };

  return (
    <div className="nc-SectionGridFeaturePlaces relative">
      <HeaderFilter
        tabActive={tabs[0]}
        subHeading={subHeading}
        tabs={tabs}
        heading={heading}
        onClickTab={handleClickTab}
      />
      <div
        className={`grid gap-6 md:gap-8 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 ${gridClass}`}
      >
        {allTours.slice(0, 20).map((tour) => renderCard(tour))}
      </div>
      {/* <div className="flex mt-16 justify-center items-center">
        <ButtonPrimary loading>Show me more</ButtonPrimary>
      </div> */}
    </div>
  );
};

export default SectionGridFeatureTours;
